.mine-notes {
  .item {
    padding: 15px;
    background-color: #f5f5f5;
    border-radius: 4px;
    cursor: pointer;
    font-size: 14px;
    color: #999;
    transition: background-color 0.3s ease-in-out;
    & + .item {
      margin-top: 20px;
    }
    .title {
      font-size: 20px;
      color: #333;
    }
    .desc {
      margin-top: 10px;
      color: #666;
    }
    .other {
      display: flex;
      align-items: center;
      margin-top: 10px;
      font-size: 12px;
      .time {
        margin-left: 10px;
      }
      .progress {
        width: 200px;
      }
      .user {
        font-size: 14px;
      }
    }
    .operate {
      display: flex;
      margin-left: 20px;
      width: 120px;
      padding: 10px 0;
      align-items: center;
      font-size: 16px;
      opacity: 0;
      transition: opacity 0.3s ease-in-out;
      .operate-item {
        display: flex;
        align-items: center;
        justify-content: space-around;
        border-radius: 50%;
        opacity: 0.7;
        &:hover {
          opacity: 1;
        }
        & + .operate-item {
          margin-left: 10px;
        }
      }
    }
    &:hover {
      background-color: #f1f1f1;
      .operate {
        opacity: 1;
      }
    }
  }
}

.setting {
  padding: 20px;
  background-color: #f5f5f5;
  border-radius: 4px;
  .setting-content {
    width: 500px;
  }
}

@primary-color: #79a8a9;@link-color: #79a8a9;@border-radius-base: 2px;